<template>
	<view>
	<!-- 标题和头部内容 -->
	<musichead title="网易云音乐" :icon="false"></musichead>
	
	<!-- 搜索歌曲 -->
	<view class="index-search">
		<text class="iconfont icon-fangdajing"></text>
		<input placeholder="搜索歌曲" />
	</view>
	
	<!-- 热歌榜 -->
	<view>
		<hottop v-for="(item,index) in mlist" 
